.highlighter-rouge .highlight {
    background-color:rgb(250, 250, 250);
}

pre, 
code {
    font-size: 15px;
    border: 1px solid #e8e8e8;
    background-color:rgb(250, 250, 250);
}

code {
    padding: 1px 5px;
}

pre {
    padding: 8px 12px;
    overflow-x: auto;

    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}

:not(a):not(pre)>code {
    background-color: hsla(0,0%,95%,1);
    border-radius: 3px;
    border-width: 0px;
}

.hljs {
    background-color:rgb(250, 250, 250);
}

.header-code {
    background-color: rgb(242, 242, 242);
    margin-top: 16px;
    height: 30px;
    border: 1px solid #e8e8e8;
    border-bottom-color: initial;
    border-bottom-style: initial;
    border-bottom-width: 0px;
    align-items: center;
    display: flex;

    .language {
        padding-left: 16px;
        padding-right: 16px;
        font-size: 0.8rem;
        font-weight: 400;
        white-space: pre;
    }    
}

.header-sql-code {
    @extend .header-code;
    overflow: hidden;

    .sql-types {   
        display: flex;     
        flex-direction: row;
        align-items: center;
        height: 30px;
        overflow-x: auto;
        overflow-y: hidden;

        .sqlLanguage {
            @extend .language;
            cursor: pointer;
        }
    
        .activeSqlLanguage {
            @extend .sqlLanguage;
            background-color: #fafafa;
            padding-top: 1rem;
            padding-bottom: 1rem;
        }    
    }    
}

mark {
    background-color: rgb(255, 241, 204);;
}

.markError {
    background-color: #ED4337;
}


code {
    font-family: SFMono-Regular,Consolas,Liberation Mono,Menlo,Courier,monospace;
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;

    span {
        color: rgb(23, 23, 23);
    }
}

.lang-csharp {

    .hljs-title {
        color: #007d9a;
    }

    .hljs-keyword {
        color: #0101FD;
    }

    .hljs-string {
        color: rgb(163, 21, 21);
    }
}

.lang-json {
    
    .hljs-attr {
        color: #0451a5;
    }

    .hljs-string {
        color: rgb(163, 21, 21);
    }
}

.lang-sql {
    
    .hljs-literal {
        color: #0101FD;
    }

    .hljs-string {
        color: rgb(163, 21, 21);
    }
}