.trialLayout {

    flex-grow: 1;
    flex-shrink: 1;

    .trialMain {
        max-width: 500px;
        width: 100%;
        padding-bottom: 5rem;

        h1 {
            padding-top: 2rem;
            padding-left: 2rem;
            padding-right: 2rem;
            padding-bottom: 0;
            text-align: center;
            font-size: 2.5rem;
            font-weight: 600;
        }

        p {
            font-size: 16px;
            font-weight: 400;
            color: $docsTextColor;
        }                 

        .trialForm {
            width: 100%;
            padding-top: 2rem;

            button {
                cursor: pointer;
                background-color: transparent;
                color: white;
                border-radius: 2px;
                font-size: .875rem;
                border: 0;
                display: block;
                width: 180px;
                text-align: center;
                height: 30px;
                font-size: 15px;
                font-weight: 600;
                padding: 0;
                background-color: #00b5dc;
                
                &:hover {
                    background-color: rgb(23, 162, 202);
                }
            }

            .trialFormContent 
            {
                max-width: 500px;
                padding-left: 20px;
                padding-right: 20px;

                .trialFormData {
                    .trialFormInputs {
                        width: 300px;
                    }
                }

                .inputForm {
                    margin-bottom: 16px;

                    label {
                        box-sizing: border-box;
                        box-shadow: none;
                        margin-top: 0px;
                        margin-right: 0px;
                        margin-bottom: 0px;
                        margin-left: 0px;
                        display: block;
                        padding-top: 5px;
                        padding-right: 0px;
                        padding-bottom: 5px;
                        padding-left: 0px;
                        font-size: 14px;
                        font-weight: 400;
                    }      
                    
                    input {
                        border-radius: 2px;
                        border: 1px solid #c8c8c8;
                        font-size: 14px;
                        padding-top: 5px;
                        padding-right: 11px;
                        padding-bottom: 5px;
                        padding-left: 11px;
                    }
                }               

                .inputError {
                    padding-top: 5px;
                    font-size: 12px;
                    color: #a4262c;
                }

                .sendButton {
                    padding-top: 2rem;
                    display: -webkit-box;
                    display: -moz-box;
                    display: -ms-flexbox;
                    display: -webkit-flex;
                    display: flex;
                    justify-content: center;
                }

                .trialTerms {
                    padding-top: 16px;
                    
                    .trialTermsCheck {
                        padding-top: 2px;
                        padding-right: 5px;
                    }

                    p {
                        font-size: 14px;
                        font-weight: 400;
                    }
                }
            }                      
        }

        .trialResponse {
            display: none;
            padding: 20px;

            .responseMessage {
                font-size: 20px;
                margin-bottom: 2rem;
            }

            .responseEmail {
                margin-top: 2rem;
                font-size: 16px;
            }

            h2 {
                font-size: 1.75rem;
                font-weight: 600;
                margin-top: 30px;
            }

            h3 {
                font-size: 1.1875rem;
                font-weight: 600;
                margin-top: 30px;
            }
        }
    }
}

.required  {
    label::after {
        content: " *";
        color: #a80000;
        padding-right: 12px;
    }
}

.popupContainer {
    display: none;
    z-index: 1000;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(22,22,22,0.2);

    .trialMessagePopup {
        display: none;
        width: 350px;
        height: 200px;
        background-color: white;
        -webkit-box-shadow:0 0 10px rgba(0,0,0,0.4);
        -moz-box-shadow:0 0 10px rgba(0,0,0,0.4); 
        box-shadow:0 0 10px rgba(0,0,0,0.4);
        top: 200px;
        left: calc(50% - 175px);
        position: fixed;
        padding: 20px; 
    }    

    .trialError {
        @extend .trialMessagePopup;

        .trialErrorLayout {
            height: 100%;

            .trialErrorTitle {
                font-size: 25px;
                font-weight: 200;                
            }

            .trialErrorMessage {
                padding-top: 10px;
                font-size: 14px;
            }

            .trialErrorButton {
                text-align: end;
            }
        }
    } 
    
    button {
        cursor: pointer;
        background-color: transparent;
        color: white;
        border-radius: 2px;
        font-size: .875rem;
        border: 0;
        width: 130px;
        text-align: center;
        height: 30px;
        font-size: 15px;
        font-weight: 600;
        padding: 0;
        outline-color: transparent;
        background-color: #973eff;
        
        &:hover {
            background-color: #971eff;
        }
    }
}
