body {
    display: flex;
    flex-basis: 100%;
    flex-direction: column;
    flex-grow: 1;
    flex-shrink: 1;
    min-height: 100vh;
    margin: 0px;
    padding: 0px;    
}

body, h1, h2, h3, h4, h5, h6, p, blockquote, pre, hr, dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}

h1, h2, h3, h4, h5, h6, p, blockquote, pre, ul, ol, dl, figure, .highlight {
    margin-bottom: 15px;
}

ul, ol {
    margin-left: 30px;
}

li {
    font-size: 16px;
    font-weight: 400;
} 

main {
    flex-grow: 1;
    flex-shrink: 1;
}

blockquote {
    background: rgb(250, 250, 250);
    border-left: 5px solid rgb(0, 101, 179);
    margin: 1.5em 10px;
    padding: 1em 10px .1em 10px;
    quotes: "\201C""\201D""\2018""\2019";

    p {
        font-size: 1em !important;
        color: rgb(23, 23, 23);
    }
}

.error-code {
    font-family: monospace;
}

.table-wrapper {
    width: 100%;
    overflow-x: auto;
    margin-bottom: 1rem;
}

.loadingContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  .loadingSymbol {
    border: .5rem solid #f3f3f3;
    border-radius: 50%;
    height: 3.75rem;
    width: 3.75rem;
    border-top: .5rem solid rgb(16, 110, 190);
    -webkit-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
  }

  @-webkit-keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
    }

    100% {
      -webkit-transform: rotate(360deg);
    }
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }