.orderCompletedLayout {

    flex-grow: 1;
    flex-shrink: 1;

    .orderCompletedMain {
        max-width: 800px;
        width: 100%;
        padding-top: 3rem;
        padding-bottom: 5rem;

        .orderCompletedWaiting {
            height: 200px;          
        }

        h1 {
            text-align: center;
        }
    }

    .orderCompletedResult {
        display: none;
    }

    .orderCompletedError {
        display: none;
    }

    p {
        font-size: 16px;
        font-weight: 400;
        color: $docsTextColor;
    }   
}
