.featuresDetailLayout {
    flex-grow: 1;
    flex-shrink: 1;

    .featureDetail {
        background-color: #f2f2f2;

        .featureDetailContent {
            background-color: white;
            max-width: 1000px;

            .cardHeader {
                padding-top: 2rem;
                padding-bottom: 1rem;

                h2 {
                    padding-left: 1rem;
                    padding-right: 1rem;
                    text-align: center;
                    color: rgb(50, 49, 48);
                    font-size: 32px;
                    font-weight: 600;
                }

                .icon {
                    @extend .alignHorizontalMiddle;

                    i {
                        font-size: 60px;
                        padding-bottom: 0.5rem;
                    }
                }
            }

            .cardContent {
                padding: 2rem;

                .noTopMarginH3 {
                    margin-top: 0;
                }

                h3 {
                    margin-top: 2rem;
                }

                .link {
                    display: block;
                    text-decoration: none;
            
                    &:hover {
                        text-decoration: underline;
                    }
                }
            
                li {
                    margin-bottom: 1rem;
                }

                p {
                    font-size: 16px;
                    font-weight: 400;
                    color: $docsTextColor;
                }
                
                .featureDetailLogos {
                    @extend .alignHorizontalMiddle;
                    padding-top: 2rem;
                    padding-bottom: 3rem;                    

                    div {
                        a {
                            padding-left: 2rem;
                            padding-right: 2rem;
                            text-decoration: none;
                            text-align: center;

                            img {
                                border: 0;
                            }
                        }
                    }
                }

                .dbFeatureDetailLogos {
                    @extend .featureDetailLogos;
                }

                .topFeatureDetailLogos {
                    padding-bottom: 0rem;                    
                }

                .middleFeatureDetailLogos {
                    padding-top: 0rem;
                    padding-bottom: 0rem;                    
                }

                .bottomFeatureDetailLogos {
                    padding-top: 3rem;
                }
            }            
        }
    }   
}

@media (max-width: 1500px) {
    main {
        .featuresDetailLayout {            
            .featureDetail {                
                .featureDetailContent {
                    max-width: 900px;
                }
            }
        }
    }    
}

@media (max-width: 1300px) {
    main {
        .featuresDetailLayout {            
            .featureDetail {                
                .featureDetailContent {
                    max-width: 850px;

                    .dbFeatureDetailLogos {
                        padding-bottom: 1rem !important;
                       
                        div {
                            a {
                                display: block;
                                padding-bottom: 2rem;
                            }

                            .squareLogo {
                                margin-top: 2rem;
                            }                        
                        }
                    }                   
                }
            }
        }
    }    
}


@media (max-width: 1200px) {
    main {
        .featuresDetailLayout {            
            .featureDetail {                
                .featureDetailContent {
                    max-width: 800px;
                }
            }
        }
    }    
}

@media (max-width: 1000px) {
    main {
        .featuresDetailLayout {            
            .featureDetail {                
                .featureDetailContent {
                    max-width: 700px;                    
                }
            }
        }
    }    
}

@media (max-width: 900px) {
    main {
        .featuresDetailLayout {            
            .featureDetail {                
                .featureDetailContent {
                    max-width: 75%;
                }
            }
        }
    }    
}

@media (max-width: 755px) {
    main {
        .featuresDetailLayout {            
            .featureDetail {                
                .featureDetailContent {
                    .featureDetailLogos {
                        padding-bottom: 1rem !important;

                        div {
                            a {
                                display: block;
                                padding-bottom: 2rem;
                            }
                        }
                    }
                }
            }
        }
    }    
}

@media (max-width: 700px) {
    main {
        .featuresDetailLayout {            
            .featureDetail {                
                .featureDetailContent {
                    max-width: 100%;
                }
            }
        }
    }    
}
