.pricingLayout {

    flex-grow: 1;
    flex-shrink: 1;

    .pricingMain {
        max-width: 1200px;
        width: 100%;
        // background-color: lightblue;

        h1 {
            padding-top: 2rem;
            padding-left: 2rem;
            padding-right: 2rem;
            padding-bottom: 0;
            text-align: center;
            font-size: 2.5rem;
            font-weight: 600;
        }

        h2 {
            font-size: 1.75rem;
            font-weight: 600;
            margin-top: 30px;
        }

        h3 {
            font-size: 1.1875rem;
            font-weight: 600;
            margin-top: 30px;
        }
        
        .pricingTry {
            
            .try {
                margin-top: 24px;
            }
        }

        .pricingContent {
            padding: 2rem;            

            .pricingCards {
                display: table;
                // background-color: beige;

                .pricingCard {
                    float: left;
                    min-width: 250px;
                    margin: 16px;
                    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);

                    .name {
                        @extend .alignVerticalMiddle;
                        @extend .alignHorizontalMiddle;
                        height: 60px;
                        
                        h4 {
                            margin-bottom: 0;
                            color: white;
                            font-size: 24px;
                            font-weight: 600;
                        }                        
                    }

                    .featuresList {
                        color: rgb(85, 85, 85);
                        padding-left: 2rem;
                        padding-right: 2rem;
                        padding-top: 1rem;
                        padding-bottom: 0.75rem;

                        ul {
                            margin: 0;
                            padding: 0;
                            list-style: none;
                            padding-block-start: 0;
                            padding-block-end: 0;
                            
                            li {                                
                                @extend .alignVerticalMiddle;
                                font-size: 14px;
                                font-weight: 400; 
                                margin-bottom: 10px;       
                            
                                i {
                                    color: green;
                                    font-size: 13px;
                                    font-weight: 600;
                                    margin-right: 6px;
                                }

                                span {
                                    @extend .alignVerticalMiddle;
                                }
                            }
                        }
                    }

                    .includedList {
                        @extend .featuresList;
                    }

                    .addedList {                        
                        @extend .featuresList;
                        border-top: 1px solid #e6e6e6;                        

                        ul {

                            li {                                
                                i {
                                    font-size: 10px;
                                }
                            }
                        }
                    }

                    .price {
                        text-align: center;
                        padding-top: 0.25rem;
                        padding-bottom: 0.25rem;
                        font-size: 36px;
                        font-weight: 400;
                        background-color: #f1f1f1;
                    }

                    .options {
                        @extend .alignHorizontalMiddle;                        
                        padding: 0.5rem; 
                                                
                        .buy {
                            background-color: #f46147;
                            margin-top: 10px;
                            margin-bottom: 10px;
                
                            &:hover {
                                background-color: #f24122;
                            }
                        }                                             
                    }
                }

                .sqlserver {
                    background-color: rgb(0, 108, 193);
                }

                .oracle {
                    background-color: #ED1B23;
                }

                .mysql {
                    background-color: #0074a3;
                }

                .postgresql {
                    background-color: #336791;
                }

                .db2 {
                    background-color: #009C49;
                }

                .sqlite {
                    background-color: #044a64;
                }

                .firebird {
                    background-color: rgb(246, 145, 29);
                }

            }
        }

        .trial {
            .try {
                margin-top: 28px;
                margin-bottom: 10px;
            }
        }

        .faq {
            margin-bottom: 5rem;
        }        

        li {
            margin-bottom: 1rem;
        }

        p {
            font-size: 16px;
            font-weight: 400;
            color: $docsTextColor;
        }

        button {
            cursor: pointer;
            background-color: transparent;
            color: white;
            text-align: left;
            border-radius: 2px;
            font-size: .875rem;
            border: 0;
            display: block;
            width: 180px;
            text-align: center;
            height: 30px;
            font-size: 15px;
            font-weight: 600;
            padding: 0;
        }

        .try {
            background-color: #00b5dc;  
            color: white;
            border-radius: 2px;
            font-size: .875rem;
            font-weight: 600;
            width: 180px;
            height: 30px;
            text-align: center;
            text-decoration: none;
            display: flex;
            flex-direction: column;
            justify-content: center;

            &:hover {
                background-color: rgb(23, 162, 202);
            }
        }
    }
}

@media (max-width: 1400px) {
    main {
        .pricingLayout {            
            .pricingMain {
                max-width: 1000px;

                .pricingContent {
                    .pricingCards {
                        max-width: 847px;
                    }
                }
            }
        }
    }    
}


@media (max-width: 1200px) {
    main {
        .pricingLayout {            
            .pricingMain {
                max-width: 800px;

                .pricingContent {
                    .pricingCards {
                        max-width: 564px;
                    }
                }   
            }
        }
    }    
}

@media (max-width: 1000px) {
    main {
        .pricingLayout {            
            .pricingMain {
                max-width: 700px;
            }
        }
    }    
}

@media (max-width: 800px) {
    main {
        .pricingLayout {            
            .pricingMain {
                padding-left: 2rem;
                padding-right: 2rem;

                .pricingContent {
                    padding-left: 0;
                    padding-right: 0;
                }   
            }
        }
    }    
}

@media (max-width: 630px) {
    main {
        .pricingLayout {            
            .pricingMain {
                .pricingContent {
                    .pricingCards {
                        max-width: 284px;
                    }
                }   
            }
        }
    }    
}
