.unsubscribeMailingLayout {

    flex-grow: 1;
    flex-shrink: 1;

    .unsubscribeMain {
        max-width: 300px;
        width: 100%;
        padding-top: 3rem;
        padding-bottom: 5rem;

        .unsubscribeWaiting {
            height: 200px;          
        }
    }

    .unsubcribeResult {
        display: none;
    }

    .unsubcribeError {
        display: none;
    }

    p {
        font-size: 16px;
        font-weight: 400;
        color: $docsTextColor;
    }   
}
