header {
    height: $headerHeight;       
    border-bottom: 1px solid #e6e6e6;

    .contextMenu {
        position: absolute;
        left: 0;
        top: $headerHeight;
        background-color: #f2f2f2;;
        display: none;
        width: 100%; 
        z-index: 99999;
        border-bottom: 1px solid rgb(0, 0, 0);  
        
        ul {
            list-style-type: none;
            margin-left: 0px;
            margin-bottom: 0px;
            overflow-x: auto;                    
            overflow-y: hidden;
            white-space: nowrap;
            padding-inline-start: 0;
            margin-block-start: 0px;
            margin-block-end: 0px;

            li {
                border-bottom: 1px solid #e6e6e6;
                
                a {
                    cursor: pointer;
                    text-decoration: none;
                    font-size: 14px;  
                    font-style: 400;
                    color: rgb(38, 38, 38);  
                    padding: 15px;
                    display: inline-block;                   
                }                        
            }
        }
    }

    .content {
        height: 100%;
        max-width: 1600px;
        width: 100%;
        
        .minContainer {            
            @extend .alignVerticalCenter;
            @extend .alignHorizontalMiddle;
            height: $headerHeight;
            margin-bottom: 2px;
            display: none; 
            padding-left: 5%;
            padding-right: 5%;
    
            .menuButton {
                margin-top: 5px;

                button {
                    background-color: transparent;
                    cursor: pointer;
                    outline: none;
                    border: 0;
                    color: #000;
                    display: block !important;
                    font-size: 15px;
                    font-weight: 600;
                    text-align: left;                
            
                    i {
                        font-size: 20px;
                    }
                }
            } 

            .productName {
                @extend .alignHorizontalMiddle;
                position: relative;
                height: 100%;

                a {
                    top: 0px;
                    position: absolute;

                    img {
                        width: 138px;
                    }
                }
            }
            
            .rightSide {
                width: 32px;
                height: 26px;
            }
        }

        .container {
            @extend .alignVerticalCenter;
            height: 100%;
            margin-bottom: 2px;            
            padding-left: 5%;
            padding-right: 5%;
    
            nav {
                height: 100%;
                width: 100%;
                ul {
                    list-style-type: none;
                    margin-left: 0px;
                    margin-bottom: 0px;
                    overflow-x: auto;                    
                    overflow-y: hidden;
                    white-space: nowrap;
                    display: flex;
                    flex-direction: row;
                    padding-inline-start: 16px;
                    margin-block-start: 0px;
                    margin-block-end: 0px;
                    height: 100%;

                    position: relative;
                    top: 5px;

                    li {
                        padding-left: 10px;
                        padding-right: 10px;

                        a {
                            text-decoration: none;
                            font-size: 13px;  
                            color: $headerColor;  
                            padding-top: 8px;
                            padding-bottom: 10px;
                            display: inline-block;
                            margin-top: 3px;                            

                            &:hover {
                                border-bottom-width: 2px; 
                                border-bottom-style: solid;                        
                                padding-bottom: 2px;
                            }
                        }                        
                    }
                }
            }

            .githubIcon {
                @extend .alignVerticalCenter;
                height: 100%;
                width: 16px;
                padding-top: 2px;

                a {
                    cursor: pointer;
                    height: 16px;
                    width: 16px; 
                                       
                    position: relative;
                    top: 5px;

                    svg {
                        height:16px;
                        width: 16px;
                    }

                    &:hover {
                        path {
                            fill:#605e5c;
                        }
                    }
                }
            }
        }

        .productName {
            // padding: 16px;

            a {
                // top: 0px;
                // position: absolute;

                img {
                    width: 138px;
                    top: 9px;
                    position: relative;
                }
            }

            // a {
            //     text-decoration: none;                    
            //     font-size: 18px;  
            //     font-weight: 600;
            //     color: $headerColor;  

            //     &:hover {
            //         border-bottom-width: 2px; 
            //         border-bottom-style: solid;                        
            //         padding-bottom: 2px;
            //     }
            // }
        }
    }
}

@media (max-width: 500px) {
    header {  
        .content {   
            .minContainer {
                display: flex;
            }

            .container {
                display: none;
            }
        }
    }
}