main { 
  
    .docsContainer {
        flex-grow: 1;
        flex-shrink: 1;

        .docsPage {
            max-width: 1600px;

            .docsMenuButton {
                display: none;
                margin-top: 1rem;
                margin-bottom: 1rem;
                margin-left: 48px;
                margin-right: 1rem;

                button {
                    height: 100%;
                    cursor: pointer;
                    background-color: transparent;
                    color: rgb(23, 23, 23);
                    text-align: left;
                    border-radius: 2px;
                    font-size: .875rem;
                    padding-bottom: calc(.375em - 1px);
                    padding-left: .75em;
                    padding-right: .75em;
                    padding-top: calc(.375em - 1px);
                    border: 1px solid #e6e6e6;
                    
                    &:hover {
                        background-color: rgb(250, 250, 250);
                    }

                    .docsMenuButtonContent {
                        @extend .alignVerticalCenter;

                        i {
                            margin-right: 10px;
                            margin-top: 2px;
                        }
                    }
                }

                .docsContextMenu {
                    z-index: 8888;
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100vh;
                    width: 100%;
                    display: none;
                    background-color: rgba(0, 0, 0, 0.5);

                    .contextMenuContent {                        
                        background-color: white;
                        width: 320px;
                        height: 100%;
                        border: 1px solid #e6e6e6;

                        .header {
                            border-bottom: 1px solid #e6e6e6;

                            .title {
                                
                                h3 {
                                    margin-bottom: 0;
                                    font-size: 16px;
                                    font-weight: 600;
                                    padding: 1rem;
                                }
                            }

                            .close {
                                border-left: 1px solid #e6e6e6;

                                button {
                                    @extend .alignHorizontalMiddle;
                                    @extend .alignVerticalMiddle;
                                    border: none;
                                    width: 50px;
                                    height: 100%;
                                }
                            }
                        }                        
                    }
                }
            }

            .docsLayout {
                flex-grow: 1;
                flex-shrink: 1;

                .docsMenu {
                    width: 300px;
                    position: sticky;
                    top: 0;
                    max-height: calc(100vh - #{$headerHeight});                
                }

                .docsMain {
                    padding-top: 2rem;
                    
                    .docsContent {
                        padding-left: 48px; 
                        padding-right: 48px; 
                        margin-bottom: 64px;  

                        h1 {
                            font-size: 2.5rem;
                            font-weight: 600;
                        }   
                        
                        h2 {
                            font-size: 1.75rem;
                            font-weight: 600;
                            margin-top: 30px;
                        }   

                        h3 {
                            font-size: 1.1875rem;
                            font-weight: 600;
                            margin-top: 30px;
                        }
                        
                        h4 {
                            font-size: 1rem;
                            font-weight: 600;
                            margin-top: 30px;
                        }
                        
                        p {
                            font-size: 16px;
                            font-weight: 400;
                            color: $docsTextColor;
                        }

                        li {
                            font-size: 16px;
                            font-weight: 400;
                            color: $docsTextColor; 
                        }
                    }
                }

                .docsIndex {
                    width: 300px;
                    position: sticky;
                    top: 0;
                    max-height: calc(100vh - #{$headerHeight});        

                    .indexBlock {
                        height: 100%;
                        overflow-y: auto;
                        padding-top: 6rem;

                        .indexTitle {
                            h3 {
                                font-size: 16px;
                                font-weight: 600;
                                padding-top: 0px;
                                padding-right: 0px;
                                padding-bottom: 0px;
                                padding-left: 8px;
                                margin-top: 0px;
                                margin-right: 0px;
                                margin-bottom: 8px;
                                margin-left: 0px;
                                color: rgb(96, 94, 92);
                            }
                        }

                        .indexContent {
                            padding-left: 1rem;
                            padding-right: 2rem; 
                            padding-bottom: 2rem;

                            .indexTopics {

                                ul {
                                    margin-top: 0px;
                                    margin-right: 0px;
                                    margin-bottom: 0px;
                                    margin-left: 0px;
                                    padding-top: 0px;
                                    padding-right: 0px;
                                    padding-bottom: 0px;
                                    padding-left: 0px;
                                    list-style: none;
                                    padding-block-start: 0px;
                                    padding-block-end: 0px;

                                    li {
                                        font-size: 14px;
                                        font-weight: 400;

                                        p {
                                            margin-bottom: 0px;                                   
                                        }

                                        a {
                                            display: block;
                                            text-decoration: none;
                                            padding-top: 4px;
                                            padding-right: 8px;
                                            padding-bottom: 4px;
                                            padding-left: 8px;
                                            color:rgb(50, 49, 48);
                                        }   
                                        
                                        .activeTopic {
                                            border-left: 2px solid rgb(0, 120, 212);                                    
                                        }

                                        a:hover {
                                            color: rgb(0, 69, 120);
                                            text-decoration: underline;
                                        }
                                    }

                                    li:hover {
                                        background: rgb(237, 235, 233);
                                    }
                                }
                            }
                        }
                    }
                }
            } 
        }  
    
    }
}

.menuBlock {
    height: 100%;
    overflow-y: auto;
    padding-top: 1.7rem;

    .menuSearch {
        padding-top: 1rem;
        padding-left: 2rem;
        padding-right: 1rem;       

        .inputSearch {
            @extend .alignVerticalMiddle;
            border: 1px solid rgb(227, 227, 227);
            border-radius: 2px;
            box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
            padding-top: calc(.375em - 1px);
            padding-bottom: calc(.375em - 1px);
            color: rgb(23, 23, 23);   
            
            &:hover {
                border-color: rgb(0, 101, 179);
            }

            .filterIcon {
                color: rgb(0, 101, 179);
                padding-left: .65rem;
                padding-right: .65rem;
            }

            .closeFilterIcon {
                color: rgb(96, 94, 92);
                font-size: 12px;
                padding-left: .65rem;
                padding-right: .65rem;
                cursor: pointer;
                display: none;

                &:hover {
                    color: rgb(0, 101, 179);
                }
            }

            input {
                width: 100%;
                height: 23px;
                border: none; 
                font-size: .875rem;                 

                &:focus {
                    outline: none;
                }
            }                     
        }

        .inputSearchActive {
            border-color: rgb(0, 101, 179);
            box-shadow: 0 0 0 .125rem rgb(0, 101, 179);
        }
    }
    
    .menuContent {
        padding-top: 1rem;
        padding-left: 2rem;
        padding-right: 1rem;
        padding-bottom: 2rem;        

        .menuChapter {
            padding: 8px 0;
            cursor: pointer;

            .icon {
                margin-top: 3px;
                margin-right: 1rem;

                i {
                    font-weight: 600;
                    font-size: 16px;
                    color: $docsChapterTitleColor;
                }
            }

            .chapterHeader {
                .chapterTitle {
                    font-size: 14px;
                    font-weight: 600;
                    color: $docsChapterTitleColor;
                }
            }

            .chapterTopics {
                padding-left: 16px;
                padding-inline-start:16px;
                display: none;
                margin-left: 0px;
                margin-bottom: 0px;

                .topic {
                    list-style-type: none;

                    a {
                        display: block;
                        text-decoration: none;
                        font-size: 14px;      
                        font-weight: 400;                                    
                        padding: 8px;
                        color: $docsTopicTitleColor;
                    }
                }

                .topic:hover {
                    background-color: $docsTopicHoverBackgroundColor;

                    a {
                        text-decoration: underline;
                    }
                }

                .activeTopic {
                    background-color: $docsActiveTopicBackgroundColor !important;
                }
            }
        }

        .openedChapter {
            .chapterHeader {
                margin-bottom: 8px;
            }

            .chapterTopics {
                display: block;
            }
        }
    }

    .menuFilteredContent {
        display: none;
        padding-top: 1rem;
        padding-left: 2rem;
        padding-right: 1rem;
        padding-bottom: 2rem;    
        
        ul {
            margin-left: 0px;
            margin-bottom: 0px;

            li {
                list-style-type: none;
                border-bottom: 1px solid #e6e6e6;
                
                a {
                    display: block;
                    text-decoration: none;
                    font-size: 14px;      
                    font-weight: 400;                                    
                    padding: 8px;
                    color: $docsTopicTitleColor;

                    .topicTitleFiltered {
                        font-size: .875rem;
                    }

                    .chapterTitleFiltered {
                        font-size: .75rem;
                        color: #605e5c;                 
                    }
                }
            }

            li:hover {
                background-color: $docsTopicHoverBackgroundColor;                
            }
        }
    }
}

.hideDocIndex {
    display: none;
}

@media (max-width: 1200px) {
    main {
        .docsContainer {
            .docsPage {
                .docsLayout {
                    .docsIndex {
                        display: none;
                        width: 0px;
                    }
                } 
            }
        }
    }
}

@media (max-width: 1000px) {
    main {  
        .docsContainer {
            .docsPage {     
                .docsLayout { 
                    .docsMenu {
                        width: 250px;
                    }        
                }
            }
        }
    }
}

@media (max-width: 800px) {
    main {  
        .docsContainer {
            .docsPage {   
                .docsMenuButton {
                    display: block;

                    .docsContextMenu {
                        .menuBlock {
                            padding-top: 0;
                        }
                    }
                }   

                .docsLayout { 

                    .docsMain {
                        padding-top: 0.5rem;
                    }                

                    .docsMenu {
                        display: none;
                        width: 0px;
                    }        
                }
            }
        }
    }
}

@media (max-width: 600px) {
    main {  
        .docsContainer {
            .docsPage {   
                .docsMenuButton {
                    margin-left: 24px;
                }
                
                .docsLayout { 

                    .docsMain {                       

                        .docsContent {
                            padding-left: 24px; 
                            padding-right: 24px; 
                        }
                    }                    
                }
            }
        }
    }
}

.docs-alert {
    padding: 1rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-color: 1px solid;
    border-radius: 6px;
    
    .alert-title {
        font-weight: 600;
        font-size: 1rem;
        display: flex;
        align-items: center;
       
        span {
            margin-left: 5px;
        }
    }

    .alert-text {
        margin-top: 1rem;
    }
}

.docs-info-alert {
    @extend .docs-alert;

    background-color: $docsInfoAlertBackgroundColor;
    border-color: $docsInfoAlertBackgroundColor;
    color: $docsInfoAlertColor;   
}

.docs-warning-alert {
    @extend .docs-alert;

    background-color: $docsWarningAlertBackgroundColor;
    border-color: $docsWarningAlertBackgroundColor;
    color: $docsWarningAlertColor;   
}

.docs-note-alert {
    @extend .docs-alert;
    background-color: $docsNoteAlertBackgroundColor;
    border-color: $docsNoteAlertBackgroundColor;
    color: $docsNoteAlertColor;  
}

.docs-table {
    width: 100%;
    max-width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: auto;
    font-size: .875rem;

    thead {
        box-sizing: border-box;

        tr {
            display: table-row;

            th {
                vertical-align: bottom;
                text-align: left;
                border-top: 0;
                border-bottom: 1px;
                border-right: 0;
                border-left: 0;
                border-style: solid;
                border-color: #e6e6e6;
                font-weight: 700;
                display: table-cell;
                word-wrap: break-word;
                padding: .75rem 1rem;
                line-height: 1.5;
            }        
        }
    }

    tbody {
        display: table-row-group;
        vertical-align: middle;

        tr {
            display: table-row;

            td {
                color: rgb(23, 23, 23);
                font-weight: 400;
                display: table-cell;
                word-wrap: break-word;
                padding: .75rem 1rem;
                line-height: 1.5;
                vertical-align: top;
                border-top: 1px;
                border-right: 0;
                border-left: 0;
                border-bottom: 0;
                border-color:#e6e6e6;
                border-style: solid;
            }
        }
    }
}

.copyClipboard {
    padding-left: 16px;    

    button {    
        width: 68px;
        padding-left: 10px;
        padding-right: 10px;
        align-items: flex-start;
        display: flex;
        font-size: 0.8rem;
        align-items: center;
        border: 0 solid rgb(227, 227, 227);
        border-left-width: 1px;
        cursor: pointer;
        height: 29px;
        background-color: #f2f2f2;

        .icon {
            padding-right: 5px;
        }

        &:hover {
            background-color:rgb(250, 250, 250);
        }

        &:focus {
            outline: none;
        }

        .iconDone {
            display: none;
        }
    }
}

.clipboardDone {
    background-color: rgb(18, 135, 18) !important;

    .copyText {
        display: none;
    }

    .icon {
        display: none;
    }

    .iconDone {
        color: white;
        font-size: 20px;
        display: block !important;
        width: 100%;

        i {
            padding-top: 4px;
        }
    }
}