main {
    width: 100%;

    .home {        
        flex-grow: 1;
        flex-shrink: 1;
        background-color: $homeBackgroundColor;

        .mainContent {
            min-height: $homeMainContentHeight;
            background-color: black;
            display: flex;
            justify-content: center;

            .centerContent {
                display: flex;
                max-width: $homeCenterContentMaxWidth;
                width: 100%;

                .leftContent {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    @extend .flexAvailable;                    

                    .leftTitle {
                        font-size: 80px;
                        color: $homeMainContentLeftTitleColor;
                        line-height: 1.1;
                        margin: 2rem;
                        font-weight: 600;
                        text-align: center;
                    }
                }

                .rightContent {
                    color: $homeMainContentRightContentColor;
                    font-size: 16px;
                    font-weight: 400;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    @extend .flexAvailable;                    
                    
                    .rightMain {
                        margin-top: 4rem;
                        max-width: 400px;
                        font-size: 18px;

                        .link {
                            @extend .alignVerticalCenter;
                            @extend .monoText;
                            height: 25px;
                            margin-bottom: 3em;                                                

                            .icon {
                                margin-right: 13px;
                            }

                            a {
                                cursor: pointer;
                                border-bottom-width: 1px; 
                                border-bottom-style: solid;
                                border-color: transparent;                        
                                padding-bottom: 2px;
                                text-decoration: none; 
                                color: $homeMainContentRightContentColor;            

                                &:hover {
                                    text-decoration: none;
                                    border-color: $homeMainContentRightContentColor;                        
                                }

                                &:visited {
                                    text-decoration: none; 
                                    color: $homeMainContentRightContentColor;            
                                }
                            }
                        }
                    }
                }
            }        
        }  
        
        .secondaryContent {
            background-color: #faf9f8;
            display: flex;
            justify-content: space-evenly;
            padding: 2rem 0;

            .secondaryItem {

                h3 {
                    font-weight: 500;
                    text-transform: uppercase;
                    color: #6d6d6d;
                }

                max-width: 300px;
                font-size: 18px;
                line-height: 26px;
                font-weight: lighter;
            }
        }

        .cardsContent {
            vertical-align: baseline;

            .cardsLayout-2 {
                display: none;
            }

            .cardsLayout-3 {
                display: none;
            }

            .cardsLayout-4 {
                display: none;
            }

            .cardsLayout-5 {
                display: none;
            }

            .cardsLayout-6 {
                display: none;
            }

            .cardContainer {
                margin-top: 0px;
                margin-bottom: 0px;

                .featuresCard {
                    @extend .card;
                    background-color: $homeFeaturesCardBackgroundColor;
                }

                .learnCard {
                    @extend .card;
                    background-color: $homeLearnCardBackgroundColor;
                }

                .providersCard {
                    @extend .card;
                    background-color: $homeProvidersCardBackgroundColor;
                }                
                
                .leftCardArea {
                    height: $homeCardHeight;
                    background: $homeFeaturesCardAreaBackgroundColor;
                }

                .rightCardArea {
                    height: $homeCardHeight;
                    background: $homeProvidersCardAreaBackgroundColor;
                }
            }
        }
    }
}

.card {
    height: $homeCardHeight;
    width: $homeCardWidth;
    padding-top: 28px;
    padding-right: 28px;
    padding-bottom: 28px;
    padding-left: 28px;

    .header {        
        i {
            font-size: 60px;
        }

        h3 {
            margin-left: 1em;
            margin-top: 0px;
            font-size: 32px;
            font-weight: 600;
            padding: 0px;
            margin-block-start: 0em;
            margin-block-end: 0em;
        }
    }

    ul {
        list-style-type: none;
        margin-left: 0px;
        margin-bottom: 0px;
        overflow-x: auto;                    
        overflow-y: hidden;
        white-space: nowrap;
        padding-top: 24px;
        padding-inline-start: 0px;
        margin-block-start: 0px;
        margin-block-end: 0px;

        li {
            @extend .alignVerticalCenter;
            @extend .monoText;
            font-size: 16px;
            font-weight: 400;
            margin-bottom: 12px;

            .icon {
                margin-right: 13px;
            }

            a {
                cursor: pointer;
                border-bottom-width: 1px; 
                border-bottom-style: solid;
                border-color: transparent;                        
                padding-bottom: 3px;
                color: $homeCardLinkColor;
                text-decoration: none;                       

                &:hover {
                    border-color: unset; 
                }

                &:visited {
                    border-color: unset; 
                }
            }
        }
    }
}

@media (max-width: 1300px) {  
    main {
        .home {
           .mainContent {
                min-height: 400px;

                .centerContent {
                    flex-direction: column;

                   .rightContent {
                        .rightMain {                      
                            margin-top: 0px;
                        }
                   }
               }
           } 
        }
    }       
}

@media (max-width: 1200px) {  
    main {
        .home {
           .cardsContent {
                .cardsLayout-1 {
                    display: none;
                }

                .cardsLayout-2, .cardsLayout-3 {
                    display: flex;
                }

                .cardsLayout-2 {
                    .rightCardArea {
                        background: $homeLearnCardAreaBackgroundColor;
                    }
                }

                .cardsLayout-3 {
                    .leftCardArea {
                        background: $homeProvidersCardAreaBackgroundColor;
                    }
                }
           }
        }
    }       
}

@media (max-width: 800px) {
    main {
        .home {
            .secondaryContent {
                flex-direction: column;
                align-items: center;
                padding: 1rem 0;

                .secondaryItem {
                    max-width: 400px;
                    margin: 1rem 0;
                }
            }

            .cardsContent {
                .cardsLayout-2, .cardsLayout-3 {
                    display: none;
                }

                .cardsLayout-4, .cardsLayout-5, .cardsLayout-6 {
                    display: flex;
                }
    
                .cardsLayout-4 {
                    .rightCardArea {
                        background: $homeFeaturesCardAreaBackgroundColor;
                    }
                }
    
                .cardsLayout-5 {
                    .leftCardArea {
                        background: $homeLearnCardAreaBackgroundColor;
                    }
    
                    .rightCardArea {
                        background: $homeLearnCardAreaBackgroundColor;
                    }
                }
    
                .cardsLayout-6 {
                    .leftCardArea {
                        background: $homeProvidersCardAreaBackgroundColor;
                    }              
                }
            }            
        }
    }       
}

@media (max-width: 700px) {
    main {
        .home {
            .mainContent {
                .centerContent {

                    .leftContent {                        
                        .leftTitle {                        
                            font-size: 68px;
                        }                        
                    }
                }
            }
        }
    }       
}

@media (max-width: 600px) {
    main {
        .home {
            .mainContent {
                min-height: 400px;

                .centerContent {

                    .leftContent {                        
                        .leftTitle {                        
                            font-size: 50px;
                            margin: 0;
                        }                        
                    }

                    .rightContent {
                        .rightMain {       
                            max-width: 300px;               
                            font-size: 16px;
                        }
                   }
                }
            }

            .secondaryContent {
            
                .secondaryItem {
                    margin: 1rem 2rem;

                    h3 {
                        font-size: 18px;
                        margin-bottom: 5px;
                    }

                    p {
                        font-size: 16px;
                    }
                }
            } 
        }
    }       
}

@media (max-width: 450px) {
    .card {
        width: 100%;
    }    
}

@media (max-width: 400px) {
    main {
        .home {
            .mainContent {
                .centerContent {
                    .leftContent {
                        .leftTitle {
                            margin: 2rem;
                        }
                    }
                }
            }
        }
    }    
}
