.legalLayout {
    flex-grow: 1;
    flex-shrink: 1;

    .legalMain {
        max-width: 1200px;
        width: 100%;

        h1 {
            padding-top: 2rem;
            padding-left: 2rem;
            padding-right: 2rem;
            padding-bottom: 0;
            text-align: center;
            font-size: 2.5rem;
            font-weight: 600;
        }

        h2 {
            font-size: 1.75rem;
            font-weight: 600;
            margin-bottom: 0;
        }

        h3 {
            font-size: 1.1875rem;
            font-weight: 600;
            margin-top: 30px;
        }
              
        .legalContent {
            padding: 2rem;            
            text-align: center;
        }                 

        p {
            font-size: 16px;
            font-weight: 400;
            color: $docsTextColor;
        }      
    }
}

.legalContainer {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 4rem;
    padding: 0 2rem;

    .legalHeader {
        margin-bottom: 2rem;
    }
    
    h1 {
        padding-top: 2rem;
        padding-left: 2rem;
        padding-right: 2rem;
        padding-bottom: 0;
        text-align: center;
        font-size: 2.5rem;
        font-weight: 600;
    }
    
    h2 {
        font-size: 1.75rem;
        line-height: 2.25rem;
        font-weight: 600;
        margin-top: 15px;        
    }

    h3 {
        font-size: 1.4rem;
        font-weight: 600;
    }

    .review {
        color: red;
    }
    
    .cky-audit-table-element {
        h5 {
            font-size: 1.2rem;
            font-weight: 600;
            margin-top: 22px;
        }
    }

    li::marker {
        font-size: 1.75rem;
        font-weight: 600;
    }
    
    li {
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 10px;

        ol {
            li::marker {
                font-size: 14px;
                font-weight: unset;
            } 
        }
    }

    ul {
        li::marker {
            font-size: 14px;
            font-weight: unset;
        }
    }   

    a.cky-banner-element {
        margin: 0 0 1rem 0;
        padding: 8px 30px;
        border: 1px solid #DEE2E6;
        box-sizing: border-box;
        border-radius: 2px;
        cursor: pointer;
        display: inline-block;

        &:hover {
            background-color: #DEE2E6;
        }
    }
}

.legalEula {
    li::marker {
        font-size: 14px;
        font-weight: unset;
    }
}

.legalTermsAndConditions {
    li::marker {
        font-size: 14px;
        font-weight: unset;
    }
}

@media (max-width: 800px) {
    main {
        .legalContainer {            
            padding: 0 2rem;
        }
    }    
}