.featuresLayout {

    flex-grow: 1;
    flex-shrink: 1;

    .featuresMain {
        max-width: 1200px;
        width: 100%;

        h1 {
            padding-top: 2rem;
            padding-left: 2rem;
            padding-right: 2rem;
            padding-bottom: 0;
            text-align: center;
            font-size: 2.5rem;
            font-weight: 600;
        }

        h2 {
            font-size: 1.75rem;
            font-weight: 600;
            margin-bottom: 0;
        }

        h3 {
            font-size: 1.1875rem;
            font-weight: 600;
            margin-top: 30px;
        }
              
        .featuresContent {
            padding: 2rem;            

            .featuresCards {
                display: grid;
                grid-template-columns: 1fr 1fr;

                .featureCard {
                    display: flex;
                    flex-direction: column;
                    margin: 16px;
                    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);                    

                    .header {
                        text-align: center;
                        padding: 1rem 0;

                        .icon {
                            @extend .alignHorizontalMiddle;
        
                            i {
                                font-size: 60px;
                                padding-bottom: 0.5rem;
                            }
                        }
                    }

                    .content {
                        padding: 1.5rem;
                        display: flex;
                        flex-direction: column;
                        height: 100%;

                        .description {
                            flex: 1 1;

                            p {
                                font-size: 14px;
                            }
                        }

                        .more {
                            display: flex;
                            justify-content: flex-end;
                        }

                        ul { 
                            list-style-type: square;
                            li {
                                font-size: 14px;
                                margin-bottom: 1rem;
                            }
                        }
                    }

                }  
                
                .crossPlatform {
                    .header {
                        background-color:  rgb(37, 142, 222);
                    }
                }

                .databaseAbstractionLayer {
                    .header {
                        background-color:  rgb(207, 143, 255);
                    }
                }

                .productivity {
                    .header {
                        background-color:  rgb(105, 229, 110);
                    } 
                }

                .netLinqExpressions {
                    .header {
                        background-color:  #ffaa44;
                    } 
                }

                .fluentApi {
                    .header {
                        background-color:  #00b7c3;
                    } 
                }

                .staticAndDynamicQueries {
                    .header {
                        background-color:  #fde7e9;
                    } 
                }
            }
        }                 

        p {
            font-size: 16px;
            font-weight: 400;
            color: $docsTextColor;
        }      
    }
}

@media (max-width: 1400px) {
    main {
        .featuresLayout {            
            .featuresMain {
                max-width: 1100px;                
            }
        }
    }    
}

@media (max-width: 1300px) {
    main {
        .featuresLayout {            
            .featuresMain {
                max-width: 100%;                
            }
        }
    }    
}

@media (max-width: 900px) {
    main {
        .featuresLayout {            
            .featuresMain {
                max-width: 600px;

                .featuresContent {
                    padding: 1rem;            

                    .featuresCards {
                        grid-template-columns: 1fr;
                    }         
                }            
            }
        }
    }    
}
