.ms-Icon {
    display: inline-block;
    font-family: FabricMDL2Icons;
    font-style: normal;
    font-weight: 400;
    speak: none
}

.ms-Icon--circle {
    margin: 0 .5em 0 0;
    position: relative;
    display: inline-block;
    font-size: 1rem;
    width: 1em;
    height: 1em;
    padding: 0;
    text-align: left
}

[dir=rtl] .ms-Icon--circle {
    margin: 0 0 0 .5em
}

.ms-Icon--circle::after,.ms-Icon--circle::before {
    line-height: 1;
    font-size: inherit
}

.ms-Icon--circle::before {
    display: block;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    position: absolute
}

.ms-Icon--circle::after {
    content: '\e000';
    position: absolute;
    top: 0;
    left: 0;
    transform: scale(2);
    transform-origin: 50% 50%;
    z-index: 0
}

.ms-Icon--xs {
    font-size: 10px
}

.ms-Icon--s {
    font-size: 12px
}

.ms-Icon--m {
    font-size: 16px
}

.ms-Icon--l {
    font-size: 20px
}

